.ReactModal__Overlay.ReactModal__Overlay--after-open.slide-pane__overlay {
  z-index: 9999; }

.menu-list {
  list-style-type: none;
  padding-left: 0; }
  .menu-list li {
    padding: 10px 0; }
    .menu-list li a {
      color: black;
      text-decoration: none; }
      .menu-list li a:visited {
        color: black; }
      .menu-list li a .active {
        color: #067e1b; }
      .menu-list li a:hover {
        cursor: pointer;
        color: #067e1b;
        text-decoration: underline; }

.modal-content {
  padding: 20px; }

.modal-fullscreen {
  max-width: 100%;
  width: 100%;
  height: 80%; }



