.modal-content {
  padding-left: 0;
  padding-right: 0;
  width: 50vw; }
  @media screen and (max-width: 1080px) {
    .modal-content {
      width: 100%;
      height: 90%; } }
  .modal-content #intro-form {
    width: 100%;
    height: 100%; }
    .modal-content #intro-form label {
      display: block;
      font-size: 1.25em; }
    .modal-content #intro-form input#title {
      display: block;
      width: 100%;
      border: none;
      border-bottom: 1px solid black; }
    .modal-content #intro-form textarea#content {
      margin-top: 1em;
      display: block;
      width: 100%;
      border: 1px solid black;
      height: 80%;
      font-size: 1.25em; }
    .modal-content #intro-form button {
      border-radius: 0;
      margin-top: 1em;
      border: 1px solid green;
      /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#a9db80+0,96c56f+100;Green+3D+%232 */
      background: #a9db80;
      /* Old browsers */
      /* FF3.6-15 */
      background: -webkit-linear-gradient(top, #a9db80 0%, #96c56f 100%);
      /* Chrome10-25,Safari5.1-6 */
      background: -webkit-gradient(linear, left top, left bottom, from(#a9db80), to(#96c56f));
      background: -o-linear-gradient(top, #a9db80 0%, #96c56f 100%);
      background: linear-gradient(to bottom, #a9db80 0%, #96c56f 100%);
      /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a9db80', endColorstr='#96c56f',GradientType=0 );
      /* IE6-9 */ }
    .modal-content .modal-content #intro-form {
      width: 100%; }
    @media screen and (max-width: 1080px) {
      .modal-content #intro-form input#title {
        width: 100%;
        font-size: 2em; } }

.location-options-list {
  list-style-type: none; }
  .location-options-list li {
    padding: 15px 0; }
    .location-options-list li a {
      color: black; }
      .location-options-list li a:visited {
        color: black; }



